	module.exports = {"data":{"markdownRemark":{"html":"<p>开学，报到。文科大楼没电，众人只好拾级而上，一片怨言。</p>\n<p>修改关于政治体制改革的英文的文章。花了一个下午的时间完成了。这台电脑里的程序很好，比中文的WPS有一些优点，能处理注释。这在中文软件中是最困难的问题。大概也和中国人写文章的方法有关系。中国人过去写文章，不太重视资料和材料所以对电脑软件也就没有这样的要求。现在不同了情况已经发生变化，文章的注释越来越多，要求也越来越高，没有就不行。不过，我相信市场的力量，大概是可以解决的，只要有市场。</p>","id":"/home/swain/workspace/political-life/src/pages/zh/0040.md absPath of file >>> MarkdownRemark","fileAbsolutePath":"/home/swain/workspace/political-life/src/pages/zh/0040.md","frontmatter":{"title":"2月23日 星期三"}}},"pathContext":{"id":"/home/swain/workspace/political-life/src/pages/zh/0040.md absPath of file >>> MarkdownRemark"}}

